.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 5rem;
  left: 50%; }

.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px; }

.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #0b4353;
  color: white; }

.modal__header h2 {
  margin: 0.5rem; }

.modal__content {
  padding: 1rem 0.5rem; }

.modal__footer {
  padding: 1rem 0.5rem; }

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem; } }

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0; }

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms; }

.modal-exit {
  transform: translateY(0);
  opacity: 1; }

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms; }

.modalButton {
  color: white;
  text-align: center;
  background: #0b4353;
  width: 5rem;
  height: 2rem;
  border-radius: 1rem;
  border: none; }

.marginleft {
  margin-left: 2rem; }

.inlineBlock {
  display: inline-block; }

.block {
  display: block; }

.verticalAlign {
  vertical-align: top; }

.carouselDisplayNone {
  display: none; }

.carouselDisplayBlock {
  display: block; }

.italicFont {
  font-style: italic; }

.textAlignCenter {
  text-align: center; }

.getFont {
